import styled from 'styled-components'
import { Link as LinkR } from 'react-router-dom';
import {Link as LinkS} from 'react-scroll'

export const Nav = styled.nav`
 background: #FBF6F5;
 
 height:80px;
 margin-top:-80x;
 display:flex;
 justify-content:center;
 align-items:center;
 font-size:rem;
 position:sticky;
 top:0;
 z-index:10;

 @media screen and (max-width: 960px) {
   transition:0.8s all ease;
 }
`;

export const NavbarContainer = styled.div`
display:flex;
justify-content: space-between;
height:80px;
z-index:1;
width:100%;
padding:0 24px;
max-width:1100px;
`;
export const NavLogo = styled.div
`
color:#000;
font-size: 2rem;
justify-self:flex-start;
cursor:pointer;
display:flex;
align-items:center;
margin-left:24px;
font-weight:bold;
`;
export const MobileIcon = styled.div`
display: none;

@media screen and (max-width: 768px) {
  display:block;
  position: absolute;
  top:0;
  right: 0;
  transform: translate(-100%,60%);
  font-size:1.8rem;
  cursor: pointer;
}
`;
export const NavMenu = styled.ul`
display:flex;
align-items: center;
list-style: none;
text-align: center;
margin-right:-22px
font-family: Verdana, sans-serif;
color: #333;

@media screen and (max-width:768px){
  display:none;
}
`;

export const NavItem = styled.li`
  height: 80px;
  
  color: #333;
`;

export const NavLinks = styled(LinkS)
`
color:#444;
font-family: Verdana, sans-serif;
display:flex;
align-items:center;
text-decoration: none;
padding: 0 1rem;
height: 100%;
cursor:pointer;

&:active {
  border-bottom: 3px solid #01bf71;
}
&:hover{
  color:#F9A826;
}
`;

export const NavBtn = styled.nav
`
display:flex;
align-items:center;

@media screen and (max-width:768px) {
  display:none;
}
`

export const NavBtnLink = styled(LinkR)`
 border-radius: 11px;
 background:	#FBF6F5
 white-space:nowrap;
 padding: 5px 15px;
 color:#010606;
 font-size: 1rem;
 outline: none;
 font-family: Verdana, sans-serif;
 border: 2px solid #222;
 cursor: pointer;
 transition: all 0.2s ease-in-out;
 text-decoration: none;


 &:hover {
   transition: all 0.2s ease-in-out;
   background:#000;
   color: #fff;
 }
`
